import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { UserService } from '../../_services/user.service';
import { AlertifyService } from '../../_services/alertify.service';
import { User } from '../../_models/user';
import { AuthService } from '../../_services/auth.service';
import { HttpClient, HttpEventType } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { MyLocation } from '../../_models/MyLocation';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  public mymodel: any = {};
  mycolor: any = {};
  user: User;
  locations: MyLocation[];
  userid: any =  Number(localStorage.getItem('UserId'));
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  baseUrl = environment.apiUrl + 'requests/';
  public uploadedimg: string;
  public message: string;
  public progress: number;
  @Output()  UploadFinished = new EventEmitter();

  constructor(private userService: UserService, private alertify: AlertifyService, private authService: AuthService,
    private http: HttpClient, private loadspecsService: LoadspecsService) { }

  ngOnInit(): void {
    this.mymodel.Country = '0';
    this.loadLocations();
    this.loadUser(this.userid);
  }
  loadLocations() {
    return this.loadspecsService.loadLocations().subscribe((locations: MyLocation[]) => {
      this.locations = locations.filter(proj => (proj.status === 1));
    }, error => {
      this.alertify.error(error);
    });
  }

  loadUser(id: number) {
    return this.userService.getUser(id).subscribe((user: User) => {
      console.log(user);
      this.mymodel.Id = user.id;
      this.mymodel.Username = user.username;
      this.mymodel.Email = user.email;
      this.mymodel.Company = user.company;
      this.mymodel.Reg_no = user.reg_no;
      this.mymodel.mobile_no = user.mobile_no;
      this.mymodel.tel_no = user.tel_no;
      this.mymodel.City = user.city;
      this.mymodel.Country = user.country;
      this.mymodel.photoUrl = user.photoUrl;
    }, error => {
      this.alertify.error(error);
    });
  }
  EdituserPost() {
    if (!this.mymodel.Email) {
      this.alertify.error('Write email');
      return;
    }
    if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.mymodel.Email)) {
      this.alertify.error('enter valid email');
      return;
    }
    if (!this.mymodel.Password) {
      this.alertify.error('Select password');
      return;
    }
    this.mymodel.RoleID = this.roleid;
    this.authService.EdituserPost(this.mymodel).subscribe((users: User[]) => {
      this.alertify.success('Password Updated Successful');
    }, error => {
      this.alertify.error(error);
    });
  }
  EdituserInfoPost() {
    if (!this.mymodel.Company) {
      this.alertify.error('Write Company');
      return;
    }
    if (!this.mymodel.Reg_no) {
      this.alertify.error('Write Regno');
      return;
    }
    if (!this.mymodel.mobile_no) {
      this.alertify.error('Write mobile');
      return;
    }
    if (!this.mymodel.City) {
      this.alertify.error('Write City');
      return;
    }
    if (!this.mymodel.Country) {
      this.alertify.error('Write Country');
      return;
    }
    console.log(this.mymodel);
    this.authService.EdituserInfoPost(this.mymodel).subscribe((users: User[]) => {
      this.alertify.success('User Edited Successful');
    }, error => {
      this.alertify.error(error);
    });
  }
  public uploadFile = (files) => {
    if (files.length === 0) {
      return;

    }
    const fileToUpload = <File>files[0];
    const formData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    if (!(fileToUpload.type.indexOf('image') !== -1)) {
      this.mycolor = 'notdone';
      this.message = 'Invalid file type';
      return;
    }
    console.log(fileToUpload.type.indexOf('image') !== -1);
    this.http.post(this.baseUrl + 'upload_profile/' + this.userid + '/', formData, {reportProgress: true, observe: 'events'})
    .subscribe((event): any => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress = Math.round(100 * event.loaded / event.total);
      } else if (event.type === HttpEventType.Response) {
        this.loadUser(this.userid);
        this.mycolor = 'completedone';
        this.message = 'Upload success';
        this.UploadFinished.emit(event.body);
        this.mymodel = event.body;
        console.log(this.mymodel.fileName);
        localStorage.setItem('Attachment', this.mymodel.fileName);
        this.uploadedimg = fileToUpload.name;
      }
    });


  }

}
